import React, { useEffect, useRef, useState } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";

const ApprovedCenterLayer = () => {
  const [centers, setCenters] = useState([]);
  const [loading, setLoading] = useState(false);

  const navigate = useNavigate();
  // Fetch only approved centres
  const fetchApprovedCentres = async () => {
    try {
      setLoading(true);
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/`,
      ); // your API route for approved centres
      setCenters(res.data || []);
    } catch (error) {
      console.error("Failed to fetch approved centres", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchApprovedCentres();
  }, []);

  // Handle delete
  const handleDelete = async (id) => {
    if (window.confirm("Are you sure you want to delete this centre?")) {
      try {
        await axios.delete(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/${id}`,
        );
        toast.success("Centre deleted successfully.");
        fetchApprovedCentres(); // Refresh list
      } catch (error) {
        console.error("Delete failed", error);
        toast.error("Failed to delete centre.");
      }
    }
  };

  const handleClick = async (centreId) => {
    navigate(`/services/${centreId}`);
  };

  const [name, setName] = useState("");
  const [type, setType] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/search`,
          {
            params: {
              name: name.trim(),
              type: type.trim(),
            },
          },
        );
        setCenters(response.data.centres || []);
      } catch (err) {
        console.error(err);
      }
    };

    fetchData();
  }, [name, type]);

  const [selectedCentre, setSelectedCentre] = useState(null);
  const [showViewModal, setShowViewModal] = useState(null);

  const modalRef = useRef(null);
  const viewModalRef = useRef(null);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showViewModal
      ) {
        setShowViewModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showViewModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showViewModal]); // <-- runs every time modal opens/closes

  const [currentPage, setCurrentPage] = useState(1);
  const centresPerPage = 10;

  const indexOfLastCentre = currentPage * centresPerPage;
  const indexOfFirstCentre = indexOfLastCentre - centresPerPage;
  const currentCentres = centers.slice(indexOfFirstCentre, indexOfLastCentre);
  const totalCentresPages = Math.ceil(centers.length / centresPerPage);

  const handlePageChangeWithScroll = (page) => {
    handlePageChange(page); // your existing page change logic
    window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
  };

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 3;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalCentresPages) {
      endPage = totalCentresPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

    pages.push(
      <li
        key="first"
        className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
        >
          First
        </button>
      </li>,
    );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)
          }
        >
          Prev
        </button>
      </li>,
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button
            className="page-link"
            onClick={() => handlePageChangeWithScroll(i)}
          >
            {i}
          </button>
        </li>,
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${
          currentPage === totalCentresPages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalCentresPages &&
            handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>,
    );

    pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalCentresPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalCentresPages)}
        >
          Last
        </button>
      </li>,
    );

    return pages;
  };

  const handlePageChange = (pageNumber) => setCurrentPage(pageNumber);

  const toTitleCase = (str = "") => {
    return str
      .toLowerCase()
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  return (
    <div className="dashboard-body__content">
      <div style={{ display: "flex", justifyContent: "flex-end" }}></div>

      <div className="card common-card border border-gray-five">
        <div className="approved-centres-header">
          <h4>Approved Centres</h4>

          <div className="search-container">
            <input
              type="text"
              className="common-input border-gray-five"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Search by Centre Name"
              style={{ flex: "1 1 200px", minWidth: "150px" }}
            />

            <select
              className="common-input border-gray-five"
              value={type}
              onChange={(e) => setType(e.target.value)}
              style={{ flex: "1 1 150px", minWidth: "150px" }}
            >
              <option value="">Select Type</option>
              <option value="Hospital">Hospital</option>
              <option value="General Clinic">General Clinic</option>
              <option value="Dental Care Centre">Dental Care Centre</option>
              <option value="Labs and Diagnostic">Labs and Diagnostic</option>
              <option value="AYUSH Centre">AYUSH Centre</option>
              <option value="Miscellaneous Healthcare Centre">
                Miscellaneous Healthcare Centre
              </option>
            </select>
          </div>
        </div>

        <div className="card-body">
          {loading ? (
            <p>Loading...</p>
          ) : centers.length > 0 ? (
            <div className="table-responsive" style={{ marginTop: "-25px" }}>
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Centre Name</th>
                    <th>Centre Type</th>
                    <th>Total Services</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {currentCentres.map((centre, index) => (
                    <tr key={centre._id}>
                      <td>{indexOfFirstCentre + index + 1}</td>
                      {/* <td
                        onClick={() => handleClick(centre._id)}
                        className="cursor-pointer"
                      >
                        {centre.centreName}
                      </td> */}
                      <td>
                        <Link
                          to={`/services/${centre._id}`}
                          style={{ textDecoration: "underline" }}
                        >
                          {toTitleCase(centre.centreName)}
                        </Link>
                      </td>
                      <td>{centre.centreType}</td>
                      <td>{centre.totalServices || 0}</td>
                      <td>
                        {/* <button
                          style={{
                            backgroundColor: "red",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                          }}
                          onClick={() => handleDelete(centre._id)}
                        >
                          Delete
                        </button> */}

                        <button
                          className="btn btn-sm btn-info"
                          onClick={() => {
                            setSelectedCentre(centre);
                            setShowViewModal("details");
                          }}
                          style={{ backgroundColor: "blue" }}
                        >
                          View
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p className="text-muted">No approved centres found.</p>
          )}
          {currentCentres.length > 0 && totalCentresPages > 1 && (
            <nav aria-label="Service pagination" className="mt-3">
              <ul className="pagination justify-content-center">
                {renderPagination()}
              </ul>
            </nav>
          )}
        </div>
      </div>
      <ToastContainer position="top-right" autoClose={3000} />

      {showViewModal && selectedCentre && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            ref={viewModalRef}
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxHeight: "95vh",
              maxWidth: "800px",
              width: "95%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "absolute",
              overflowY: "scroll",
              /* Hide scrollbar cross-browser */
              scrollbarWidth: "none", // Firefox
              msOverflowStyle: "none",
            }}
            className="no-scrollbar"
          >
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position: "sticky",
                top: "0",
                zIndex: 10,
              }}
            >
              Centre Details
              <button
                onClick={() => setShowViewModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            <div
              style={{
                display: "flex",
                flexWrap: "wrap",
                padding: "20px",
                gap: "20px",
                color: "#333",
              }}
            >
              {/* Left Column */}
              <div style={{ flex: 1, minWidth: "250px" }}>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Centre Name</strong>
                  <p>{selectedCentre.centreName}</p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Centre Type</strong>
                  <p>{selectedCentre.centreType}</p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Email</strong>
                  <p>{selectedCentre.email}</p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Phone</strong>
                  <p>{selectedCentre.phoneNumber}</p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Landline</strong>
                  <p>
                    {selectedCentre.landlineNumber || "Landline not available"}
                  </p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Address</strong>
                  <p>{selectedCentre.address}</p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>ZIP Code</strong>
                  <p>{selectedCentre.zipCode}</p>
                </div>

                <div style={{ marginBottom: "15px" }}>
                  <strong>City</strong>
                  <p>{selectedCentre.city}</p>
                </div>

                <div style={{ marginBottom: "15px" }}>
                  <strong>State</strong>
                  <p>{selectedCentre.state}</p>
                </div>
              </div>

              {/* Right Column */}
              <div style={{ flex: 1, minWidth: "250px" }}>
                <div style={{ marginBottom: "15px" }}>
                  <strong style={{ color: "black" }}>Facilities</strong>
                  <ul style={{ marginTop: "5px" }}>
                    {selectedCentre.availableFacilities?.length ? (
                      selectedCentre.availableFacilities.map(
                        (facility, index) => <li key={index}>{facility}</li>,
                      )
                    ) : (
                      <li>None</li>
                    )}
                  </ul>
                </div>

                <div style={{ marginBottom: "15px" }}>
                  <strong>DigiPin Location</strong>
                  <p>
                    {selectedCentre.digiPinLocation || "DigiPin not available"}
                  </p>
                </div>
                <div style={{ marginBottom: "15px" }}>
                  <strong>Authorized Person</strong>
                  <p>
                    Name: {selectedCentre.authorizedPerson?.name || "N/A"}{" "}
                    <br />
                    Designation:{" "}
                    {selectedCentre.authorizedPerson?.designation || "N/A"}
                  </p>
                  {selectedCentre.authorizedPerson?.authDocument && (
                    <a
                      href={selectedCentre.authorizedPerson.authDocument}
                      target="_blank"
                      rel="noopener noreferrer"
                      style={{
                        display: "inline-block",
                        marginTop: "5px",
                        fontSize: "14px",
                        color: "#007bff",
                      }}
                    >
                      View Document
                    </a>
                  )}
                </div>

                {selectedCentre.documents?.length > 0 && (
                  <div style={{ marginBottom: "15px" }}>
                    <strong style={{ color: "black" }}>Documents</strong>
                    <ul style={{ marginTop: "5px" }}>
                      {selectedCentre.documents.map((doc, index) => (
                        <li key={index}>
                          {doc.docType} -{" "}
                          <a
                            href={doc.docFile}
                            target="_blank"
                            rel="noopener noreferrer"
                            style={{ color: "#007bff", fontSize: "14px" }}
                          >
                            View Document
                          </a>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      <style>{`.approved-centres-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 20px 30px;
  gap: 10px;
}

.approved-centres-header h4 {
  margin: 0;
  font-weight: bold;
  color: #333;
  flex: 1 1 auto; /* heading takes space */
}

.approved-centres-header .search-container {
  display: flex;
  gap: 10px;
  flex: 1 1 auto;
  justify-content: flex-end;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .approved-centres-header h4 {
    flex: 1 1 100%; /* full width on mobile */
    margin-bottom: 10px;
  }
  .approved-centres-header .search-container {
    flex: 1 1 100%; /* full width on mobile */
    justify-content: flex-start;
  }
}
`}</style>
    </div>
  );
};

export default ApprovedCenterLayer;
